   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  padding: 25px;
}

div#grid-wrapper {
  gap: 50px;
}

div#grid-wrapper {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(4, 1fr);
}

div#local-weather-data {
  grid-column: 1 / span 3;
  display: flex;

  padding: 50px;

  border-radius: 20px;
  border: 0px solid #fff;
  background: rgba(217, 217, 217, 0.15);
  box-shadow: 0px 4px 6.8px 1px rgba(155, 155, 155, 0.25);
  backdrop-filter: blur(9.899999618530273px);
}

div#vertical-container {
  display: flex;
  flex-direction: column;
}

div#quick-weather-overview-container {
  display: flex;
  flex-direction: row;
}

input#location-input {
  grid-row: 1 / span 1;
  grid-column: 4 / span 4;
  width: 638px;
  height: 51px;
  flex-shrink: 0;
  padding: 10px;
}
